/* General Navbar Styles */
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #2bf12b; /* Forest green theme */
    padding: 10px 20px;
    color: rgb(0, 0, 0);
    font-family: Arial, sans-serif;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  }
  
  .logo {
    font-size: 1.5rem;
    font-weight: bold;
    display: flex;
    align-items: center;
  }
  
  .logo span {
    margin-left: 10px;
  }
  
  .nav-links {
    list-style: none;
    display: flex;
    gap: 15px;
    margin: 0;
    padding: 0;
  }
  
  .nav-links li {
    display: inline;
  }
  
  .nav-links a {
    text-decoration: none;
    color: rgb(2, 1, 1);
    font-size: 1rem;
    transition: color 0.3s ease-in-out;
  }
  
  .nav-links a:hover {
    color: #189f09; /* Light green hover effect */
  }
  
  .donate-btn {
    background-color: #ff6347; /* Tomato red for emphasis */
    padding: 8px 15px;
    border-radius: 5px;
    font-weight: bold;
    text-transform: uppercase;
    transition: background-color 0.3s ease-in-out;
  }
  
  .donate-btn:hover {
    background-color: #ff4500; /* Darker red on hover */
  }
  