.container-login {
    width: 300px;
    margin: 50px auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
    background-color: #f9f9f9;
    margin-top:40px;
    
  }
  .container-login h1{
    color: green;
    margin-left:50px;
  }

  .saytree
  {
    margin-left:400px;
    margin-top:90px;
    font-family: 'Times New Roman', Times, serif;
    font-size:80px;
    color: rgb(0, 0, 0);
  }
  
  h2 {
    text-align: center;
    margin-bottom: 20px;
    color: #333;
  }
  
  form div {
    margin-bottom: 15px;
  }
  
  label {
    display: block;
    font-weight: bold;
    margin-bottom: 5px;
    color: #555;
  }
  
  input {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 16px;
  }
  .register-btn {
    margin-top: 10px;
    padding: 10px;
    background-color: #f32ed2;
    color: white;
    border: none;
    cursor: pointer;
  }
  
  .register-btn:hover {
    background-color: #2ad672;
  }
  
.logibtn{
    width: 100%;
    padding: 10px;
    background-color: #4CAF50;
    border: none;
    border-radius: 5px;
    color: white;
    font-size: 16px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #45a049;
  }
  
  p {
    color: red;
    text-align: center;
  }
  