.container-regi {
    width: 300px;
    margin: 50px auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
    background-color: #f1f1f1;
    margin-top:40px;
  }
  
  .container-regi h1{
    color: green;
    margin-left:50px;
  }

  .welcome-regi
  {
    margin-left:400px;
    margin-top:90px;
    font-family: 'Times New Roman', Times, serif;
    font-size:80px;
    color: rgb(0, 0, 0);
  }
  h2 {
    text-align: center;
    margin-bottom: 20px;
    color: #444;
  }
  
  form div {
    margin-bottom: 15px;
  }
  
  label {
    display: block;
    font-weight: bold;
    margin-bottom: 5px;
    color: #666;
  }
  
  input {
    width: 100%;
    padding: 10px;
    border: 1px solid #aaa;
    border-radius: 5px;
    font-size: 16px;
  }
  
  button {
    width: 100%;
    padding: 10px;
    background-color: #007BFF;
    border: none;
    border-radius: 5px;
    color: white;
    font-size: 16px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #0056b3;
  }
  
  p {
    text-align: center;
    margin-top: 15px;
  }
  
  p.success {
    color: green;
  }
  
  p.error {
    color: red;
  }
  